export default {
  methods: {
    copyText() {
      navigator.clipboard
        .writeText(this.$refs.text.innerHTML)
        .then((result) => {
          console.log(this.$refs.text.innerHTML)
        })
        .catch((error) => {
          console.log(error)
        })
    },
  },
}
